<template>
  <div class="todo pb-12">
    <v-container style="max-width: 600px">
      <v-btn fab text small @click="switchDarkMode" class="d-block ml-auto">
        <v-icon>{{
          darkMode ? "mdi-white-balance-sunny" : "mdi-weather-night"
        }}</v-icon>
      </v-btn>
      <v-snackbar v-model="repeated" top>
        <template v-slot:action="{ on, attrs }">
          <v-btn
            text
            @click="$store.state.newStore.repeated = false"
            v-on="on"
            v-bind="attrs"
            small
            ><v-icon>mdi-close</v-icon></v-btn
          >
        </template>
        <span>Repeated Task</span>
      </v-snackbar>
      <!-- <v-snackbar
        :style="{ marginBottom: `${i * 60}px` }"
        v-model="repeated"
        v-for="(item, i) in errors"
        :key="i"
      >
        <span>Repeated</span>
      </v-snackbar> -->
      <v-card class="mt-12">
        <v-text-field
          @keydown.enter="create"
          v-model="newTask"
          label="What are you working on?"
          solo
        >
          <template v-slot:append>
            <v-fade-transition>
              <v-icon @click="create" v-if="newTask">mdi-plus-circle</v-icon>
            </v-fade-transition>
          </template>
        </v-text-field>
        <v-card-text class="success--text pt-0">
          <h3 class="text-h4 pl-4 d-flex">
            Tasks:
            <v-fade-transition leave-absolute>
              <span :key="`tasks-${tasks.length}`" class="text-h4">{{
                tasks.length
              }}</span>
            </v-fade-transition>
            <v-spacer></v-spacer>
            <v-btn
              color="error darken-2"
              :disabled="tasks.length == 0"
              @click="A_removeAll"
              ><span class="subtitle-2 font-weight-light">Remove All</span>
              <v-icon right>mdi-delete-outline</v-icon></v-btn
            >
          </h3>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-text class="">
          <v-row align="center" justify="space-around">
            <strong class="primary--text"
              >Remaining:
              <v-fade-transition leave-absolute>
                <span :key="remaining">{{ remaining }}</span>
              </v-fade-transition>
            </strong>
            <v-divider vertical></v-divider>
            <strong class="success--text"
              >Completed:
              <v-fade-transition leave-absolute>
                <span :key="completed">{{ completed }}</span>
              </v-fade-transition>
            </strong>
            <v-progress-circular
              :value="allCompleted"
              class="mr-4"
            ></v-progress-circular>
          </v-row>
        </v-card-text>
        <v-divider></v-divider>
        <v-card>
          <!-- <v-slide-y-transition group tag="v-list"> -->
          <TransitionGroup name="fade" tag="ul" class="pa-0">
            <template v-for="(item, i) in tasks">
              <v-divider :key="`${i}-divider`" v-if="i !== 0"></v-divider>
              <v-hover v-slot="{ hover }" :key="`${i}-hover`">
                <v-list-item :key="item.title" class="pr-2">
                  <v-list-item-action>
                    <v-checkbox
                      v-model="item.done"
                      :color="item.done ? 'grey' : 'primary'"
                      @click="saveToLocal()"
                    >
                      <template v-slot:label>
                        <div
                          style="min-width: 75px"
                          :class="[
                            'ml-4',
                            item.done ? 'grey--text' : 'primary--text',
                            ,
                          ]"
                        >
                          {{ item.title }}
                        </div>
                      </template>
                    </v-checkbox>
                  </v-list-item-action>
                  <v-spacer></v-spacer>
                  <v-fade-transition class="hidden-sm-and-down">
                    <span
                      v-show="hover"
                      class="hidden-sm-and-down mr-2 grey--text text--darken-1"
                      >{{ item.title.length <= 35 ? item.today : "" }} {{
                    }}</span>
                  </v-fade-transition>
                  <v-scroll-x-transition>
                    <div style="min-width: 25px">
                      <v-icon color="success" v-if="item.done"
                        >mdi-check</v-icon
                      >
                    </div>
                  </v-scroll-x-transition>
                  <v-btn
                    class="ml-3 hidden-sm-and-down"
                    color="error"
                    icon
                    @click="removeItem(i)"
                    ><v-icon v-show="hover">mdi-close</v-icon></v-btn
                  >
                  <v-btn
                    class="ml-3 hidden-md-and-up"
                    color="error"
                    icon
                    @click="removeItem(i)"
                    ><v-icon>mdi-close</v-icon></v-btn
                  >
                </v-list-item>
              </v-hover>
            </template>
          </TransitionGroup>
          <!-- </v-slide-y-transition> -->
        </v-card>
        <!-- START -->
        <!-- END -->
      </v-card>
    </v-container>
    <div class="links px-5">
      <v-btn
        icon
        target="_blank"
        :color="item.color"
        v-for="(item, i) in items.socials.slice(1, 4)"
        :href="item.link"
        :key="i"
      >
        <v-icon :class="[i == 1 && !darkMode ? 'white--text' : '']">{{
          item.icon
        }}</v-icon>
      </v-btn>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import data from "@/db/data.json";
export default {
  data() {
    return {
      newTask: "",
      items: data,
    };
  },
  mounted() {
    if (localStorage.getItem("tasks")) {
      this.A_getFromLocal();
    }
    let mode = JSON.parse(localStorage.getItem("darkMode"));
    if (mode) {
      this.$store.state.newStore.darkMode = mode;
      this.$vuetify.theme.dark = mode;
    }
  },
  //**Methods */
  methods: {
    ...mapActions([
      "A_addTask",
      "A_toggleDone",
      "A_getFromLocal",
      "A_removeAll",
      "A_removeItem",
    ]),
    create() {
      if (this.newTask) {
        this.A_addTask(this.newTask);
        this.saveToLocal();
        this.newTask = "";
      }
    },
    saveToLocal() {
      localStorage.setItem("tasks", JSON.stringify(this.tasks));
    },
    removeItem(ind) {
      this.A_removeItem(ind);
      this.saveToLocal();
    },
    switchDarkMode() {
      this.$store.state.newStore.darkMode =
        !this.$store.state.newStore.darkMode;
      this.$store.state.newStore.darkMode
        ? [
            (this.$vuetify.theme.dark = true),
            localStorage.setItem("darkMode", true),
          ]
        : [
            (this.$vuetify.theme.dark = false),
            localStorage.setItem("darkMode", false),
          ];
    },
  },
  //**Computed */
  computed: {
    ...mapState({
      tasks: (store) => store.newStore.tasks,
      repeated: (store) => store.newStore.repeated,
      // errors: (store) => store.newStore.errors,
      darkMode: (store) => store.newStore.darkMode,
    }),
    allCompleted() {
      let arrLength = this.tasks.length;
      let sol = this.tasks.filter((el) => {
        return el.done;
      }).length;
      // console.log((2 / 4) * 100);
      return (sol / arrLength) * 100;
    },
    remaining() {
      let sol = this.tasks.filter((el) => {
        return !el.done;
      });
      return sol.length;
    },
    completed() {
      let sol = this.tasks.filter((el) => {
        return el.done;
      });
      return sol.length;
    },
  },
};
</script>

<style lang="scss" scoped>
@import "../../scss/_main.scss";
.links {
  position: fixed;
  left: 50%;
  bottom: 10px;
  // width: 100px;
  // height: 100px;
  background: linear-gradient(to bottom, #555, #000);
  border-radius: 10px;
  transform: translateX(-50%);
}
</style>
