<script>
/* eslint-disable no-unused-vars */
import data from "@/db/data.json";
import mixitup from "mixitup";
export default {
  data() {
    return {
      items: data,
      tab: null,
      activeBtn: 1,
      hoverBtn: 1,
      activeSection: 1,
      darkTheme: false,
      media: false,
    };
  },
  mounted() {
    let home = document.querySelector(".home");
    let theClass = localStorage.getItem("dark");
    home.setAttribute("class", `home ${theClass}`);
    if (theClass) {
      this.darkTheme = true;
    }
    let mixerOne = mixitup(".product_parent", {
      selectors: {
        target: ".product_item",
      },
      animation: {
        duration: 300,
      },
    });
  },
  methods: {
    changeColor() {
      this.darkTheme = !this.darkTheme;
      let home = document.querySelector(".home");
      let res = localStorage.getItem("dark");
      home.classList.toggle("dark");
      // localStorage.setItem
      if (home.classList.contains("dark")) {
        localStorage.setItem("dark", "dark");
      } else {
        localStorage.removeItem("dark");
      }
    },
  },
  created() {
    window.onresize = function () {
      if (window.matchMedia("(max-width:440px)").matches) {
        this.media = true;
      } else {
        this.media = false;
      }
      console.log(this.media);
    };
  },
};
</script>

