export const newStore = {
  state: {
    tasks: [],
    repeated: false,
    darkMode: false,
  },
  getters: {
    completedTasks(state) {
      return state.tasks.filter((el) => {
        return el.done;
      }).length;
    },
  },
  mutations: {
    addTask(state, taskTitle) {
      const today = new Date();
      const year = today.getFullYear();
      const month = today.getMonth() + 1;
      const day = today.getDate();
      // const second = today.getSeconds();
      const newToday = `${day}/${month}/${year}`;
      state.repeated = false;

      if (state.tasks.length > 0) {
        state.tasks.forEach((el) => {
          if (el.title == taskTitle) {
            state.repeated = true;
          }
        });
        //
        if (!state.repeated) {
          state.tasks.push({
            title: taskTitle,
            done: false,
            today: newToday,
          });
        }
      } else {
        state.tasks.push({
          title: taskTitle,
          done: false,
          today: newToday,
        });
      }
    },
    toggleDone(state, ind) {
      state.tasks[ind].done = !state.tasks[ind].done;
    },
    getFromLocal(state) {
      state.tasks = JSON.parse(localStorage.getItem("tasks"));
    },
    removeAll(state) {
      state.tasks = [];
      localStorage.removeItem("tasks");
    },
    removeItem(state, index) {
      state.tasks.splice(index, 1);
    },
  },
  actions: {
    A_addTask({ commit }, taskTitle) {
      commit("addTask", taskTitle);
    },
    A_toggleDone({ commit }, ind) {
      commit("toggleDone", ind);
    },
    A_getFromLocal({ commit }) {
      commit("getFromLocal");
    },
    A_removeAll({ commit }) {
      commit("removeAll");
    },
    A_removeItem({ commit }, index) {
      commit("removeItem", index);
    },
  },
};
